  .simplefavorite-button-count {
    @apply ml-1 text-neutral-900 dark:text-neutral-200;
  }
}

.simplefavorite-button.active {
  @apply relative min-w-[68px] flex items-center rounded-full transition-colors px-3 h-8 text-xs focus:outline-none text-rose-600 bg-rose-50 dark:bg-rose-100 cursor-pointer;
  .simplefavorite-button-count {
    @apply ml-1 text-rose-600;
  }
}

